<script>
  import Case from "../components/Case.svelte";
  import { onMount } from "svelte";
  import { fade } from "svelte/transition";

  let canvasPortfolio, offscreenCanvas, ctx, offscreenCtx, i, prevX, prevY;
  let innerWidth;
  let innerHeight;
  let current;
  let mounted = false;
  let strokes;
  let brushRadiusX;
  let brushRadiusY;
  let clientX;
  let clientY;

  let update = event => {
    clientX = event.clientX;
    clientY = event.clientY;
    if (isNaN(clientX) || isNaN(clientY)) {
      if (event.touches) {
        if (
          event.touches[0] &&
          event.touches[0].clientX &&
          event.touches[0].clientY
        ) {
          return requestAnimationFrame(
            startTouch.bind(
              null,
              event.touches[0].clientX,
              event.touches[0].clientY
            )
          );
        }
      } else {
        return;
      }
    }
    current.forEach(brush => {
      for (i = 0; i < strokes; i++) {
        offscreenCtx.lineWidth = Math.random() * 70 + 10;
        offscreenCtx.beginPath();
        brush.x = clientX + (Math.random() * 100 - 50);
        brush.y = clientY + (Math.random() * 100 - 50);
        offscreenCtx.fillStyle = `rgba(${~~(
          (brush.x / innerWidth) *
          255
        )}, ${~~((brush.y / innerHeight) * 255)}, ${~~(
          (brush.y / innerHeight) *
          255
        )}, 0.2)`;
        offscreenCtx.ellipse(
          brush.x,
          brush.y,
          brushRadiusX,
          brushRadiusY,
          (Math.random() * 6) / Math.PI,
          0,
          2 * Math.PI
        );
        offscreenCtx.fill();
        offscreenCtx.closePath();
      }
    });
    ctx.save();
    ctx.filter = "blur(250px)";
    ctx.drawImage(offscreenCanvas, 0, 0);
    ctx.restore();
    ctx.fillStyle = "rgba(30, 30, 30, 0.3";
    ctx.fillRect(0, 0, innerWidth, innerHeight);
  };

  let startTouch = (x, y) => {
    for (let i = 0; i < 25; i++) {
      setTimeout(update({ clientX: x + i * 10, clientY: y + i * 10 }), i * 16);
    }
  };

  onMount(() => {
    mounted = true;
  });

  $: if (innerWidth && mounted) {
    strokes = 10;
    offscreenCanvas = document.createElement("canvas");
    canvasPortfolio.width = offscreenCanvas.width = innerWidth;
    canvasPortfolio.height = offscreenCanvas.height = innerHeight;
    brushRadiusX = innerWidth / 35;
    brushRadiusY = innerHeight / 35;
    current = [
      {
        x: innerWidth / 2,
        y: innerHeight / 2
      },
      {
        x: innerWidth / 2,
        y: innerHeight / 2
      },
      {
        x: innerWidth / 2,
        y: innerHeight / 2
      },
      {
        x: innerWidth / 2,
        y: innerHeight / 2
      },
      {
        x: innerWidth / 2,
        y: innerHeight / 2
      }
    ];
    ctx = canvasPortfolio.getContext("2d");
    // console.log("CCONTEXT", offscreenCtx)
    offscreenCtx = offscreenCanvas.getContext("2d");
  }
</script>

